                      </div>
                      <div class="content-item-header-email">
                        &lt; {{commits[0].author.email}} &gt;
                      </div>
                    </div>
                    <div class="content-item-body">
                      <div class="content-item-body-item"
                           v-for="(commit, index) in commits"
                           :key="commit.commit">
                        <div class="body-item-index">
                          {{index + 1}}.
                        </div>
                        <div class="body-item-content">
                          {{commit.message.replace(/[\r\n]/g, '')}}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </transition>
            <transition name="fade">
              <div class="result-chart"
                   v-if="!isGenerating && currentRepo.name && showChart">
                <log-chart :commits="commitInfo"></log-chart>
              </div>
            </transition>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Tooltip, Icon, Poptip, Switch, Input, Modal, Button, Form, FormItem, Select, Option, Spin, DatePicker, Circle } from 'view-design'
export default {
  name: 'Create',
  components: {
    Tooltip, Icon, Poptip,
    iSwitch: Switch,
    iCircle: Circle,
    Input, Modal, Button, Form, FormItem, Select, Option, Spin, DatePicker,
    LogChart: () => import('../components/LogChart')
  },
  sockets: {
    'LIST_REPOSITORIES' (data) {
      this.repositories = data.repositories
    },
    'GENERATE_PROGRESS' (data) {
      this.generateProgress = Number(data.progress)
    },
    'COMMIT_INFO' (data) {
      this.commitInfo = this.formatCommitInfo(data.info)
      setTimeout(() => {
        this.isGenerating = false
        this.generateProgress = 0
      }, 1500)
    }
  },
  data () {
    return {
      repositories: [],
      isGenerating: false, // 
      generateDate: [],
      inputManually: false, // 
      activeRepo: 'default', // 
      generateProgress: 0, // 
      commitInfo: {}, // 
      currentRepo: {}, // 
      generateFormData: {
        name: '',
        url: '',
        username: '',
        password: '',
        date: []
      },
      reportDateOptions: {
        shortcuts: [
          {
            text: '',
            value () {
              let now = new Date()
              let start = new Date()
              let end = new Date()
              var weekday = now.getDay() || 7 // ,getDay() 0  6 0||77weekday1-7
              start.setDate(now.getDate() - weekday - 7 + 1) // weekday-1
              start.setHours(0)
              start.setMinutes(0)
              start.setSeconds(0)
              end.setTime(start.getTime() + 7 * 24 * 60 * 60 * 1000 - 1000)
              return [start, end];
            }
          },
          {
            text: '',
            value () {
              let now = new Date()
              let start = new Date()
              let end = new Date()
              var weekday = now.getDay() || 7 // ,getDay() 0  6 0||77weekday1-7
              start.setDate(now.getDate() - weekday + 1) // weekday-1
              start.setHours(0)
              start.setMinutes(0)
              start.setSeconds(0)
              end.setTime(start.getTime() + 7 * 24 * 60 * 60 * 1000 - 1000)
              return [start, end];
            }
          },
          {
            text: '',
            value () {
              let now = new Date()
              let year = now.getFullYear()
              let month = now.getMonth() + 1
              let start = new Date(year + '-' + month + '-1 0:0:0')
              let nextMonth
              let nextYear = year
              if (month < 12) {
                nextMonth = month + 1
              } else {
                nextMonth = 1
                nextYear += 1
              }
              let end = new Date(nextYear + '-' + nextMonth + '-1 0:0:0')
              end.setTime(end.getTime() - 1000)
              return [start, end]
            }
          },
          {
            text: '3',
            value () {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              return [start, end];
            }
          },
          {
            text: '6',
            value () {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 2 * 3600 * 1000 * 24 * 90);
              return [start, end];
            }
          },
          {
            text: '1',
            value () {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 2 * 2 * 3600 * 1000 * 24 * 90);
              return [start, end];
            }
          }
        ]
      },
      userMap2: [
        {
          user: '',
          alias: ['', '', 'haixu.bo', 'haixu.bo', 'haixu.bo@zhaopin.com.cn', 'haixu.bo@zhaopin.com.cn']
        },
        {
          user: '',
          alias: ['', '1yue.wang', '1yue.wang', '1yue.wang@zhaopin.com.cn', '1yue.wang@zhaopin.com.cn']
        },
        {
          user: '',
          alias: ['', '', 'zhuping.yuan', 'zhuping.yuan', 'zhuping.yuan@zhaopin.com.cn', 'zhuping.yuan@zhaopin.com.cn']
        },
        {
          user: '',
          alias: ['', '', 'jianxin.wang', 'jianxin.wang', 'jianxin.wang@zhaopin.com.cn', 'jianxin.wang@zhaopin.com.cn']
        },
        {
          user: '',
          alias: ['', '', 'minxia.zhao', 'minxia.zhao', 'minxia.zhao@zhaopin.com.cn', 'minxia.zhao@zhaopin.com.cn']
        },
        {
          user: '',
          alias: ['', '', 'zengqiang.li', 'zengqiang.li', 'zengqiang.li@zhaopin.com.cn', 'zengqiang.li@zhaopin.com.cn']
        },
        {
          user: '',
          alias: ['', 'wang.wq', 'wang.wq', 'wang.wq@zhaopin.com.cn', 'wang.wq@zhaopin.com.cn']
        },
        {
          user: '',
          alias: ['', 'liang.shan', 'liang.shan', 'liang.shan@zhaopin.com.cn', 'liang.shan@zhaopin.com.cn']
        }
      ],
      userMap: {
        '': ['', '', 'haixu.bo', 'haixu.bo', 'haixu.bo@zhaopin.com.cn', 'haixu.bo@zhaopin.com.cn'],
        '': ['', '1yue.wang', '1yue.wang', '1yue.wang@zhaopin.com.cn', '1yue.wang@zhaopin.com.cn'],
        '': ['', '', 'zhuping.yuan', 'zhuping.yuan', 'zhuping.yuan@zhaopin.com.cn', 'zhuping.yuan@zhaopin.com.cn'],
        '': ['', '', 'jianxin.wang', 'jianxin.wang', 'jianxin.wang@zhaopin.com.cn', 'jianxin.wang@zhaopin.com.cn'],
        '': ['', '', 'minxia.zhao', 'minxia.zhao', 'minxia.zhao@zhaopin.com.cn', 'minxia.zhao@zhaopin.com.cn'],
        '': ['', '', 'zengqiang.li', 'zengqiang.li', 'zengqiang.li@zhaopin.com.cn', 'zengqiang.li@zhaopin.com.cn'],
        '': ['', 'wang.wq', 'wang.wq', 'wang.wq@zhaopin.com.cn', 'wang.wq@zhaopin.com.cn'],
        '': ['', 'liang.shan', 'liang.shan', 'liang.shan@zhaopin.com.cn', 'liang.shan@zhaopin.com.cn']
      },
      showChart: true // 
    }
  },
  created () {
    this.listRepositories()
    let date = this.getThisWeekDate()
    this.generateDate = date
    this.generateFormData.date = date.map(item => item.getTime())
  },
  methods: {
    getThisWeekDate () {
      let now = new Date()
      let start = new Date()
      let end = new Date()
      var weekday = now.getDay() || 7 // ,getDay() 0  6 0||77weekday1-7
      start.setDate(now.getDate() - weekday + 1) // weekday-1
      start.setHours(0)
      start.setMinutes(0)
      start.setSeconds(0)
      end.setTime(start.getTime() + 7 * 24 * 60 * 60 * 1000 - 1000)
      return [start, end];
    },
    listRepositories () {
      this.$exec({
        action: 'list-repositories'
      })
    },
    setInputManually (flag) {
      this.inputManually = flag
    },
    changeRepoUrl () {
      if (!this.inputManually) {
        // 
        this.generateFormData.name = this.generateFormData.url.split('/').pop()
      }
    },
    changeRepository (repo) {
      if (repo.value !== 'default') {
        this.generateFormData.name = repo.label
        this.generateFormData.url = repo.value
      } else {
        this.generateFormData.name = ''
        this.generateFormData.url = ''
      }
    },
    generate () {
      // 
      this.generateFormData.date = this.generateDate.map(item => item.getTime())
      this.isGenerating = true
      this.$exec({
        action: 'generate',
        data: this.generateFormData
      })
      this.currentRepo = JSON.parse(JSON.stringify(this.generateFormData))
      this.setInputManually(false)
    },
    getName (name) {
      let userMap = JSON.parse(JSON.stringify(this.userMap))
      let outName = name
      for (let k in userMap) {
        if (userMap.hasOwnProperty(k)) {
          if (userMap[k].indexOf(name) > -1) {
            outName = k
          }
        }
      }
      return outName
    },
    formatCommitInfo (info) {
      let outInfo = {}
      let i = 0
      for (i; i < info.length; i++) {
        let name = this.getName(info[i].author.name)
        if (!outInfo.hasOwnProperty(name)) {
          outInfo[name] = []
        }
        outInfo[name].push(Object.assign({}, info[i], {
          author: {
            name: info[i].author.name.replace(/["']/g, ''),
            email: info[i].author.email.replace(/["']/g, '')
          }
        }))
      }
      return outInfo
    },
    toggleCommitsType () {
      this.showChart = !this.showChart
    }
  }
}
</script>

